import axios from 'axios';
import React, { Component } from 'react';
class SendPage extends Component {
constructor(props){
    super(props)
    this.state={
        createuser:{
           name:'',
           job:''
        }
    }
}
onHandleInput=(event) => {
    this.setState({
        createuser:{...this.state.createuser,[event.target.name]:event.target.value}

    })
}
oncreateuser(){
    console.log(this.state.createuser);
    const url="https://reqres.in/api/users";
    axios.post(url,this.state.createuser)
    .then((response) =>{
        console.log(response)
    })
    .catch((error) => {
        console.log(error)
    });



}
render(){
    return(
        <div>
            <h1>this is sent page</h1>
        <div>
     <label>Enter user name:</label>
     <input type="text" name="name" placeholder="please enter user name.." onChange={this.onHandleInput}/>
     </div>
     <div>
     <label>Enter user password:</label>
     <input type="text" name="name" placeholder="please enter password.." onChange={this.onHandleInput}/>
     </div>
     <div>
         <button onClick={() => this.oncreateuser()}>createusers</button>
     </div>
     </div>
     
    );   
}
}
   

   
export default SendPage ;