import React ,{Component} from 'react';
import {NavLink} from 'react-router-dom';

class LoginPage extends Component{
    constructor (props){
        super (props);
            this.state={
                login:{
                    email:'',
                    password:''
                },
                error:{
                    email:false,
                    password:false
                }           
        }
    }
    onHandleInput=(event) =>{
        this.setState({
            login:{ ...this.state.login,[event.target.name]:event.target.value

            }
        })
    }
    onLogin(){
        for(let key in this.state.login){
            if (key=='email'){
                const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                
                this.state.error.email=!re.test(String(this.state.login[key]).toLowerCase());
            }
            else if(key=="password"){
            this.state.error.password=this.state.login[key].length>=6?false:true;     
             }
        }
        this.setState({
            error:this.state.error
        });
        for(let key in this.state.error){
            if(this.state.error[key]){
                return;
            }
        }
        this.props.history.push('/home');
    }
    goToMailBoxPage(){
        this.props.history.push("/mail/inbox", this.state.login);
      }
    render(){
        return(
       <div>
           <h1>This is login page</h1>
           <div>
               <div>
                   <label>enetr your email:</label>
<input type="text" name="email" placeholder="please enter your email"
onChange={this.onHandleInput}/>
{this.state.error.email && <span className="err-msg">please enter valid email</span>}
</div>
<div>
<label>enetr your password:</label>
<input type="text" name="password" placeholder="please enter your password"
onChange={this.onHandleInput}/>
{this.state.error.email && <span className="err-msg">please enter valid passwoed</span>}
</div>
<div>
    <button onClick={()=> this.onLogin()}>login</button>
    <NavLink to="/mail/inbox">Go to mail box Page</NavLink>
</div>
<div>
    <button onClick={() => this.goToMailBoxPage()}>mailBoxPage</button>
</div>
     </div>  
     </div>


        );
    }
}
export default LoginPage;
