import React, { Component } from 'react';
import Child from '../component/child';

class Parent extends Component {
    constructor(props){
        super(props);
        this.state={
            username:"john victor",
            message:''
        }
    }
    onRecieveName=(value)=>{
        console.log(value);
        this.setState({
            message:value
        })
    }
    render() {
        return (
            <div>
                <h1>newmessage:{this.state.message}</h1>
               <h1>this is parent component</h1> 
               <Child name={this.state.username}{...this.state}onLoadName={this.onRecieveName}></Child>
            </div>
        );
    }
}

export default Parent;