import React,{Component} from 'react';
import { NavLink } from 'react-router-dom';
class RegisterPage extends Component{
    constructor (props){
        super (props);
        this.state={
            register:{
            firstName:'',
            lastName:'',
            email:'',
            password:'',
            dateofbirth:'',
            hobbies:[],
            gender:'',
            city:'',
            address:''
            },
            error:{
            firstName:false,
            lastName:false,
            email:false,
            password:false,
            dateofbirth:false,
            hobbies:false,
            gender:false,
            city:false,
            address:false
            }

        }
    }
    onhandleinput=(event) => {
        if (event.target.type==="checkbox"){
         if(event.target.checked){
             this.state.register.hobbies.push(event.target.value);
         }
             else{
                 const index=this.state.register.hobbies.findIndex((value,index) =>
                 {
                     return value===event.target.value
                 });
                 this.state.register.hobbies.splice(index,1);
                }
                 this.setState(
                     {
                         hobbies:this.state.register.hobbies
                     }
                 )
                    }
                 else
                 {
        
        this.setState ({
            register:{...this.state.register,[event.target.name]:event.target.value}
        })
    }
}
    oncreateAccont (){
        console.log(this.state.register)
        for(let key in this.state.register){
            if(key === 'hobbies'){
            if(this.state.register.hobbies.length === 0){
                this.state.error.hobbies = true;
              }
              else{
                this.state.error.hobbies = false;
              }
            }
        
      else  if (this.state.register[key]===''){
           this.state.error[key]=true;

        }
        else{
            this.state.error[key]=false;
    }
}
        this.setState({
            error:this.state.error
        })
    }
    OnInputFocus=(event) => {
      
        this.setState({
            error:{...this.state.error,[event.target.name]:false}
        })
    }
    OnInputBlur=(event) => {
        
        if (event.target.value.length===0){
            this.setState({
                error:{...this.state.error,[event.target.name]:true}
            })
        }
    }
    render (){
       
        return(

            <div className="registerform">
                <h1>welcome to RegisterPage</h1>
                <div className="m-bottom">
                    <label className="label">Enter your firstName:</label>
                    <input type="text"  className="input" name="firstName" placeholder="please enter your first name " onChange={this.onhandleinput} onFocus={this.OnInputFocus} onBlur={this.OnInputBlur}/>
                    {
                        this.state.error.firstName && <div> <span className="err-msg"> please enter a valid firstName</span> </div>
                    }
                </div>
                <div className="m-bottom">
                    <label className="label">Enter your lastName:</label>
                    <input type="text" className="input" name="lastName"  placeholder="please enter your last name " onChange={this.onhandleinput} onFocus={this.OnInputFocus} onBlur={this.OnInputBlur}/>
                    {
                        this.state.error.lastName && <div> <span className="err-msg"> please enter a valid lastName</span> </div>
                    }
                </div>
                <div className="m-bottom">
                    <label className="label">Enter your email:</label>
                    <input type="text" className="input"  name="email" placeholder="please enter your emailid " onChange={this.onhandleinput} onFocus={this.OnInputFocus} onBlur={this.OnInputBlur}/>
                    {
                        this.state.error.email && <div> <span className="err-msg"> please enter a valid email</span> </div>
                    }
                </div>
                <div className="m-bottom">
                    <label className="label">Enter your password:</label>
                    <input type="text" className="input" name="password" placeholder="please enter your password" onChange={this.onhandleinput} onFocus={this.OnInputFocus} onBlur={this.OnInputBlur}/>
                    {
                        this.state.error.password && <div> <span className="err-msg"> please enter a valid password</span> </div>
                    }
                </div>
                <div className="m-bottom">
                    <label className="label">select your d.o.b:</label>
                    <input type="date" name="dateofbirth" className="input" onChange={this.onhandleinput}/>
                    {
                        this.state.error.dateofbirth && <div> <span className="err-msg"> please select a valid d.o.b</span> </div>
                    }
                </div>
                <div className="m-bottom">
                    <label className="label">select your gender:</label>
                    <input type="radio" name="gender" value="male" onChange={this.onHandleInput}/>Male
                    <input type="radio" name="gender" value="female" onChange={this.onHandleInput} />Female
                    <input type="radio" name="gender" value="don't want specify" onChange={this.onHandleInput}/>Others
                    {
                        this.state.error.gender && <div> <span className="err-msg"> please select a valid gender</span> </div>
                    }
              </div>
              <div className="m-bottom">
                    <label className="label">select your hobbies:</label>
                    <input type="checkbox" value="cricket"  name="hobbies" onChange={this.onHandleInput}/>cricket
                    <input type="checkbox"  value="carrom" name="hobbies"  onChange={this.onHandleInput}/>carrom
                    <input type="checkbox" value="chess" name="hobbies" onChange={this.onHandleInput}/>chess
                    <input type="checkbox" value="cinema news" name="hobbies" onChange={this.onHandleInput} />cinema news
                    {
                        this.state.error.hobbies && <div> <span className="err-msg"> please select a hobbies</span> </div>
                    }
                 </div>
                    <div className="m-bottom">
                    <label className="label">Enter your Address:</label>
                    <textarea name="address"  placeholder="please enter your address.."  onChange={this.onhandleinput} onFocus={this.OnInputFocus} onBlur={this.OnInputBlur}/>
                    {
                        this.state.error.address && <div> <span className="err-msg"> please enter a valid address</span> </div>
                    }
                    </div>
                    <div className="m-bottom">
                    <label className="label">select your city:</label>
                    <select className="input" defaultValue="" name="city" onChange={this.onhandleinput}>
                        <option value="" disabled >please your city</option>
                        <option value="chennai">chennai</option>
                        <option value="delhi">delhi</option>
                        <option value="pune">pune</option>
                        <option value="cochin">cochin</option>
                        
                    </select>
                    {
                        this.state.error.city&& <div> <span className="err-msg"> please select a valid city </span> </div>
                    }
                    </div>
                    <div>
                        <button onClick={() => {this.oncreateAccont()}}>Create my Account</button>
                    </div>
                    
                    <NavLink to ="/home">create your account</NavLink>     
            </div>
        );
    }
}
export default RegisterPage;