import React, { Component } from 'react';

class LifeCycle extends Component {
    constructor(props){
        super(props);
        console.log("CONSTRUCTOR METHOD");
        this.state={
            name:'mr.mk'
        };
    }
    componentWillMount(){
        console.log("Component will mount method");
    }
    componentDidMount(){
        console.log("Component Did mount method");
    }
onclickBtn(){
    this.setState({
        name:'mr.karthick'
    })
}
shouldComponentUpdate(nextProps,nextState){
    console.log(this.state.name,nextState); 
    console.log("shouldComponentUpdate method");  
    if(this.state.name==nextState.name){
        return false;
    }
    else{
        return true;
    }
}
componentWillUpdate(){
    console.log("Component will Update method"); 
}
componentDidUpdate(){
    console.log("Component Did Update method"); 
}
componentWillUnmount(){
    console.log("Component will Unmount method"); 
}

render() {
    console.log("Render method"); 
        return (
            <div>
                <h1>This is a lifecycle component</h1>
                <h1>The username is{this.state.name}</h1>
                <button onClick={() => this.onclickBtn()}>Update user name</button>
            </div>
        );
    }
}

export default LifeCycle;
